export const login = (credential) => {
    // login  credential
    // credential  username  password 
    // { username: 'Jerry', password: '123' }
    const loginUrl = `/login?username=${credential.username}&password=${credential.password}`;
    // loginUrl '/login?username=Jerry&password=123'
    //  username  password  credential 

    return fetch(loginUrl, {
        // fetch  API HTTP 
        method: 'POST', // HTTP  POST
        headers: { // HTTP 
            'Content-Type': 'application/json', // HTTP  JSON
        },
        credentials: 'include', // fetch  Cookie credentials  'include'
        //  body  JSON 
    }).then((response) => {
        // fetch  Promise 
        if (response.status < 200 || response.status >= 300) {
            //  HTTP  2xx
            throw Error("Fail to log in");
        }
    });
};

export const signup = (data) => {
    // signup  data
    // data  username  password 
    const signupUrl = '/signup';
    // signupUrl  API 
    // '/signup'

    return fetch(signupUrl, { //  fetch  HTTP 
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
    }).then((response) => {
        if (response.status < 200 || response.status >= 300) {
            throw Error("Fail to sign up");
        }
    });
};

export const getMenus = (restId) => {
    return fetch(`/restaurant/${restId}/menu`).then((response) => {
        if (response.status < 200 || response.status >= 300) {
            throw Error("Fail to get menus");
        }
        return response.json();
    });
};

