import {login} from "../utils";
import {Button, Form, Input, message} from "antd";
import {UserOutlined} from "@ant-design/icons";
import React from "react";


class LoginForm extends React.Component{
    // React.Component  React 
    state = {
        // state 
        //  this.state 
        loading: false,
        // loading 
        //  this.state.loading 
        // false 
    };

    onFinish = (data) => {
        // onFinish  Form  onFinish 
        // onFinish  data 
        this.setState({
            // setState  React  state
            //  this.setState  state
            loading: true, //  loading  true
        });
        //  login 
        login(data)
            .then((res) => { // 
                message.success(`Login Successful`);
                this.props.onSuccess(); //  onSuccess 
                // onSuccess  App  state isLogin  true
                //  App  componentDidUpdate 
                // props  this.props 
            })
            .catch((err) => { // 
                message.error(`err.message`);
                // message  antd 
            })
            .finally(() => {
                // loading  false
                this.setState({
                    // setState  React  state
                    //  this.setState  state
                    loading: false, //  loading  false
                });
            });
    };

    render() { // render 
        return(
            <Form // Form  antd 
                name="normal_login" // 
                onFinish={this.onFinish} // 
                style={{ // 
                    width: 300,
                    margin: "auto", // 
                }}
            >
                <Form.Item
                    name="username"
                    rules={[{
                        required: true,
                        message: "Please input your Username!",
                    }]}//  rules
                    // rules 
                    // required 
                >
                    <Input prefix={<UserOutlined />} placeholder="Username" />
                    {/*// Input  antd */}
                    {/*// prefix */}
                    {/*// placeholder */}
                    {/*// <UserOutlined />  antd */}

                </Form.Item>

                <Form.Item
                    name="password"
                    rules={[{ required: true,
                        message: "Please input your Password!"
                    }]}
                >
                    <Input prefix={<UserOutlined />} type="password" placeholder="Password" />
                </Form.Item>

                <Form.Item>
                    <Button type="primary" htmlType="submit"
                        loading={this.state.loading}
                    >
                        Login
                    </Button>
                </Form.Item>
            </Form>
        );
    };
}

export default LoginForm;